	module.exports = {"data":{"post":{"html":"<p>Mama got an air cast today (not a gel cast like we thought). It's fastened with Velcro, so she can take it off when it gets uncomfortable. She didn't even have to visit the doctor. She just picked it up from the medical supply store. Meanwhile she's able to put just a slight amount of weight on the injured foot. Several folks have commented that it must be extremely painful. It was, but only the first day. Since then, she has been mostly exhausted from all the hopping and sliding.</p>\n<p>She's missing a monthly meeting tomorrow, but her colleagues have put their entire IT department in motion to set up a webcam connection for her so she can participate. I know how much IT people <em>love</em> last minute special assignments. </p>\n<p>Meanwhile, Oma is helping out by picking up Christopher in the afternoon, so I can work a normal work day. When I get home, I get to chase after both Mama and Christopher, so I end up exhausted as well. Otherwise I'd comment on how <a href=\"http://news.bbc.co.uk/1/hi/world/europe/2683409.stm\">Germany and France are joining together push the US out of the United Nations</a>. Why else would they announce now that they will not vote for a war resolution?</p>","fields":{"title":"Hobble Hobble","date":"22 Jan 2003","url":"https://papascott.de/2003-01-22-hobble-hobble/"}}},"pathContext":{"slug":"/2003-01-22-hobble-hobble/","prev":{"fields":{"title":"Diagnosis T14.3","date":"20 Jan 2003","slug":"/2003-01-20-diagnosis-t143/"}},"next":{"fields":{"title":"Axis of Small Mammals","date":"24 Jan 2003","slug":"/2003-01-24-axis-of-small-mammals/"}}}}

